<template>
    <div class="blog-container">
        <!-- 主视图 -->
        {{id}}
    </div>
</template>
<script>

    export default {
        data() {
            return {
                id:this.$route.query.id
            }
        },
        methods: {
          
        },
    }
</script>
<style>
    .blog-list-container {
        overflow-y: auto;
    }

    .table-header {
        flex: 0 0 48px;
        display: flex;
        align-items: center;
        background-color: #f9fafc;
        padding: 0 70px;
        border-top: 1px solid #eee;
        border-bottom: 1px solid #eee;
        font-size: 12px;
        color: #a3b4bc;
    }

    .table-header :first-child {
        flex-grow: 1;
    }

    .table-header :last-child {
        flex: 0 0 7rem;
        text-align: right;
    }
</style>


